import React from "react";
import PropTypes from "prop-types";
import {useSelector} from "react-redux";
import {makeStyles} from "@material-ui/styles";
import {modulesSelector} from "../../selectors";
import Module from "./Module";

const useStyles = makeStyles(() => ({
  list: {
    "padding": 0,
    "maxHeight": `450px`,
    "overflowY": `scroll`,
    "&::-webkit-scrollbar": {
      width: `5px`,
      backgroundColor: `#f5f5f5`
    },
    "&::-webkit-scrollbar-track": {
      backgroundColor: `#f0f4f7`
    },
    "&::-webkit-scrollbar-thumb": {
      backgroundColor: `#ccc`,
      borderRadius: `5px`
    }
  },
  item: {
    borderBottom: `1px solid #eee`
  },
  link: {
    "color": `inherit`,
    "textDecoration": `none`,
    "display": `flex`,
    "&:focus": {
      outline: 0
    }
  }
}));

export default function ModuleList(props) {
  const {content} = useSelector(modulesSelector);
  const classes = useStyles();
  return (
    <div ref={props.provided.innerRef} className={classes.list}>
      {content.map((mod, index) => (
        <Module key={mod.id} mod={mod} index={index} />
      ))}
      {props.provided.placeholder}
    </div>
  );
}

ModuleList.propTypes = {
  provided: PropTypes.object
};
