import React, {useState} from "react";
import PropTypes from "prop-types";
import {useDispatch} from "react-redux";
import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import ExpansionPanelDetails from "@material-ui/core/ExpansionPanelDetails";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import {makeStyles} from "@material-ui/core/styles";
import {IconButton} from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/DeleteOutlineOutlined";
import {changeModuleTitle} from "../../AC/change-module-title";
import {changeModuleVariable} from "../../AC/change-module-variable";
import {removeModuleFromDocument} from "../../AC/remove-module-from-document";

const useStyles = makeStyles((theme) => ({
  root: {
    "&:after": {
      content: `" "`,
      width: `50px`,
      height: `100%`,
      position: `absolute`,
      left: `100%`,
      top: `0`,
      zIndex: `0`
    }
  },
  textField: {
    width: `100%`,
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1)
  },
  input: {
    "&:after": {
      display: `none`
    },
    "&:before": {
      display: `none`
    }
  },
  disableFocused: {
    background: `none !important`
  },
  icon: {
    color: `#E93333`,
    position: `absolute`,
    right: `-50px`,
    top: 0,
    zIndex: 1
  }
}));

const Panel = (props) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [deleteButtonisShow, showDeleteButton] = useState(false);
  const {uuid, title, variables} = props;
  const changeTitle = (id) => (e) => {
    dispatch(changeModuleTitle(id, e.target.value));
  };
  const changeVariable = (id, name) => (e) => {
    dispatch(changeModuleVariable(id, name, e.target.value));
  };
  const handleClick = (id) => () => {
    dispatch(removeModuleFromDocument(id));
  };
  return (
    <ExpansionPanel
      {...props}
      ref={props.innerRef}
      onMouseEnter={() => showDeleteButton(true)}
      onMouseLeave={() => showDeleteButton(false)}
      className={classes.root}
    >
      <ExpansionPanelSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls={`panel-${uuid}-content`}
        id={`panel-${uuid}-header`}
        className={classes.disableFocused}
      >
        <TextField
          required
          error={title ? false : true}
          helperText={title ? `` : `This field must be filled`}
          id="outlined-name"
          type="text"
          onChange={changeTitle(uuid)}
          className={classes.hiddenTextField}
          value={title}
        />
        {deleteButtonisShow ? (
          <IconButton className={classes.icon} onClick={handleClick(uuid)}>
            <DeleteIcon />
          </IconButton>
        ) : null}
      </ExpansionPanelSummary>
      <ExpansionPanelDetails>
        <Grid container>
          {variables.map(({type, name, value}) => (
            <Grid item xs={12} key={name}>
              <TextField
                id="outlined-name"
                label={name}
                type={type}
                multiline={type === `textarea`}
                className={classes.textField}
                value={value}
                onChange={changeVariable(uuid, name)}
                margin="normal"
                variant="outlined"
              />
            </Grid>
          ))}
        </Grid>
      </ExpansionPanelDetails>
    </ExpansionPanel>
  );
};
Panel.propTypes = {
  innerRef: PropTypes.func,
  uuid: PropTypes.string,
  title: PropTypes.string,
  variables: PropTypes.array
};

export default Panel;
