import {updateDocument} from "./update-document";
import uuid from "uuid";

export const moveModuleToDocument = (sourceIndex, distinationIndex) => (
    dispatch,
    getState
) => {
  const {
    modules: {content: modules},
    document: {content: document}
  } = getState();
  const createdDocument = {...modules[sourceIndex], uuid: uuid.v4()};
  const documentClone = Array.from(document);

  documentClone.splice(distinationIndex, 0, createdDocument);
  dispatch(updateDocument(documentClone));
};
