import React from "react";
import PropTypes from "prop-types";
import {makeStyles} from "@material-ui/styles";
import {useDispatch, useSelector} from "react-redux";
import DocIcon from "./DocIcon";
import {MenuItem} from "@material-ui/core";
import {Draggable} from "react-beautiful-dnd";
import {selectModules} from "../../AC/select-modules";
import {modulesSelector} from "../../selectors";

const useStyles = makeStyles(() => ({
  item: {
    background: `#fff`,
    borderBottom: `1px solid #eee`
  }
}));

export default function Module({mod, index}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const handleClick = (id) => () => {
    dispatch(selectModules(id));
  };
  const {selected} = useSelector(modulesSelector);
  return (
    <Draggable draggableId={mod.id + ``} index={index}>
      {(provided) => (
        <MenuItem
          ref={provided.innerRef}
          {...provided.draggableProps}
          {...provided.dragHandleProps}
          selected={selected[mod.id]}
          className={classes.item}
          onClick={handleClick(mod.id)}
        >
          <DocIcon type={mod.type} />
          {mod.title}
        </MenuItem>
      )}
    </Draggable>
  );
}

Module.propTypes = {
  mod: PropTypes.object,
  index: PropTypes.number
};
