import React from "react";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import ArrowIcon from "@material-ui/icons/ArrowRightAltOutlined";
import {useSelector, useDispatch} from "react-redux";
import {modulesSelector} from "../../selectors";
import {makeStyles} from "@material-ui/core/styles";
import {Grid} from "@material-ui/core";
import NoModules from "./NoModules";
import ModuleList from "./ModuleList";
import {moveSelectedModulesToDocument} from "../../AC/move-selected-modules-to-document";

const useStyles = makeStyles(() => ({
  paper: {
    marginBottom: `20px`
  },
  button: {
    borderRadius: `80px`
  }
}));

function Modules(props) {
  const classes = useStyles();
  const {content} = useSelector(modulesSelector);
  const dispatch = useDispatch();
  const handleClick = () => {
    dispatch(moveSelectedModulesToDocument);
  };

  return content.length ? (
    <>
      <Paper className={classes.paper}>
        <ModuleList {...props} />
      </Paper>
      <Grid container justify="flex-end">
        <Grid item>
          <Button
            variant="outlined"
            aria-label="Add"
            color="primary"
            size="large"
            onClick={handleClick}
            className={classes.button}
          >
            Add to document
            <ArrowIcon />
          </Button>
        </Grid>
      </Grid>
    </>
  ) : (
    <NoModules />
  );
}

export default Modules;
