import {
  GET_MODULES,
  START,
  SUCCESS,
  FAIL,
  SELECT_MODULES,
  CHANGE_ORDER_OF_MODULES,
  UNSELECT_MODULES
} from "../constants";

const defaultModules = {
  loading: false,
  loaded: false,
  selected: {},
  content: []
};

export default (modules = defaultModules, action) => {
  const {type, payload} = action;

  switch (type) {
    case GET_MODULES + START:
      return {...modules, loading: true, loaded: false};

    case GET_MODULES + SUCCESS:
      return {
        ...modules,
        content: payload.data,
        loading: false,
        loaded: true
      };

    case GET_MODULES + FAIL:
      return {
        ...modules,
        error: payload.error
      };

    case UNSELECT_MODULES:
      return {
        ...modules,
        selected: {}
      };

    case SELECT_MODULES:
      return {
        ...modules,
        selected: {
          ...modules.selected,
          [payload.id]: modules.selected[payload.id]
            ? !modules.selected[payload.id]
            : true
        }
      };

    case CHANGE_ORDER_OF_MODULES:
      return {
        ...modules,
        content: [...payload.modules]
      };
  }

  return modules;
};
