import {
  CHANGE_MODULE_TITLE,
  CHANGE_MODULE_VARIABLE,
  REMOVE_MODULE_FROM_DOCUMENT,
  UPDATE_DOCUMENT
} from "../constants";

const defaultDocument = {
  loading: false,
  loaded: false,
  content: []
};

export default (document = defaultDocument, action) => {
  const {type, payload} = action;

  switch (type) {
    case REMOVE_MODULE_FROM_DOCUMENT:
      return {
        ...document,
        content: [
          ...document.content.filter((item) => item.uuid !== payload.uuid)
        ]
      };

    case CHANGE_MODULE_TITLE:
      return {
        ...document,
        content: document.content.map((item) => {
          if (item.uuid === payload.uuid) {
            item.title = payload.title;
          }
          return item;
        })
      };

    case CHANGE_MODULE_VARIABLE:
      return {
        ...document,
        content: document.content.map((item) => {
          if (item.uuid === payload.uuid) {
            item.variables = item.variables.map((variable) => {
              if (variable.name === payload.name) {
                variable.value = payload.value;
              }
              return variable;
            });
          }
          return item;
        })
      };

    case UPDATE_DOCUMENT:
      return {
        ...document,
        content: [...payload.document]
      };
  }

  return document;
};
