import React from "react";
import {useSelector, useDispatch} from "react-redux";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Modules from "../Modules";
import Document from "../Document";
import {modulesSelector, documentSelector} from "../../selectors";
import {DragDropContext, Droppable} from "react-beautiful-dnd";
import "./style.css";
import {changeOrderOfModules} from "../../AC/change-order-of-modules";
import {updateDocument} from "../../AC/update-document";
import {unselectModules} from "../../AC/unselect-modules";
import {moveModuleToDocument} from "../../AC/move-module-to-document";

const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

function App() {
  const {content: modules} = useSelector(modulesSelector);
  const {content: document} = useSelector(documentSelector);
  const dispatch = useDispatch();

  function onDragStart() {
    dispatch(unselectModules);
  }

  function onDragEnd(result) {
    const {source, destination} = result;

    if (
      !destination ||
      (source.droppableId === destination.droppableId &&
        destination.index === source.index) ||
      (source.droppableId !== destination.droppableId &&
        source.droppableId === `document`)
    ) {
      return;
    }
    const modulesOrdered = reorder(
        modules,
        result.source.index,
        result.destination.index
    );
    if (
      source.droppableId === destination.droppableId &&
      source.droppableId === `modules`
    ) {
      dispatch(changeOrderOfModules(modulesOrdered));
    }
    const documentOrdered = reorder(
        document,
        result.source.index,
        result.destination.index
    );
    if (
      source.droppableId === destination.droppableId &&
      source.droppableId === `document`
    ) {
      dispatch(updateDocument(documentOrdered));
    }

    if (
      source.droppableId !== destination.droppableId &&
      source.droppableId === `modules`
    ) {
      dispatch(moveModuleToDocument(source.index, destination.index));
    }
  }
  return (
    <div className="App">
      <Grid container spacing={3}>
        <Grid item xs={4}>
          <Box fontSize={40} fontWeight={100} m={0}>
            Modules
          </Box>
        </Grid>
        <Grid item xs={8}>
          <Box fontSize={40} fontWeight={100} m={0}>
            Result document
          </Box>
        </Grid>
        <DragDropContext onDragStart={onDragStart} onDragEnd={onDragEnd}>
          <Droppable droppableId="modules">
            {(provided) => (
              <Grid item xs={4}>
                <div ref={provided.innerRef} {...provided.droppableProps}>
                  <Modules provided={provided} />
                </div>
              </Grid>
            )}
          </Droppable>
          <Droppable droppableId="document">
            {(provided) => (
              <Grid item xs={8}>
                <div ref={provided.innerRef} {...provided.droppableProps}>
                  <Document provided={provided} />
                </div>
              </Grid>
            )}
          </Droppable>
        </DragDropContext>
      </Grid>
    </div>
  );
}

export default App;
