import {updateDocument} from "./update-document";
import uuid from "uuid";
import {unselectModules} from "./unselect-modules";

export const moveSelectedModulesToDocument = (dispatch, getState) => {
  const {
    modules: {content: modules, selected},
    document: {content: document}
  } = getState();
  const selectedArray = Object.keys(selected);
  const createdModules = selectedArray.map((id) => ({
    ...modules[id],
    uuid: uuid.v4()
  }));

  dispatch(updateDocument([...document, ...createdModules]));
  dispatch(unselectModules);
};
